import React from 'react';
import styled from 'styled-components';
import { ColorPanel as ColorPanelComp } from '@ali/we-design';

type IColorPanelCompProps = React.ComponentProps<typeof ColorPanelComp>;

export type IColorPanelProps = Pick<IColorPanelCompProps, 'color' | 'defaultColor' | 'onColorClick' | 'borderColor' | 'defaultBorderColor' | 'onBorderColorClick' | 'backgroundColor' | 'defaultBackgroundColor' | 'onBackgroundColorClick' | 'checkboxLayout' >;

const ColorPanelWrapper = styled.div`
  background: white;
  padding: 8px;
  border-radius: 4px;
`;

const blockEvent = (event: React.MouseEvent) => {
  event.stopPropagation();
  event.preventDefault();
};

export function ColorPanel(props: IColorPanelProps) {

  return (
    <ColorPanelWrapper onMouseDown={blockEvent} onClick={blockEvent}>
      <ColorPanelComp
        {...props}
        enableResetButton
      />
    </ColorPanelWrapper>
  );
}